@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;400;600&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Nunito Sans", sans-serif;
}

h2 {
  margin-bottom: 10px;
  color: #ddd;
  font-size: 2rem;
  text-transform: capitalize;
}

.App {
  display: grid;
  height: 100vh;
  justify-items: center;
  align-items: center;
  background-color: #293241;
}

.alpaca-cont {
  display: grid;
  grid: 420px / auto auto;
  padding: 20px;
  max-width: 900px;
  justify-items: center;
  margin: 0 10px;
  border-radius: 10px;
}

.settings-cont {
  padding: 10px 20px;
}

.img-cont {
  display: grid;
  justify-items: center;
  align-items: center;
}

.img-cont button {
  height: auto;
  border: none;
  margin: 5px;
  padding: 10px 15px;
  border: 1px solid #aa33dd;
  background-color: transparent;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 600;
  transition: 0.3s all linear;
  color: #aa33dd;
}

.img-cont button:active {
  transform: scale(1.1);
}

.canvas-img {
  width: 300px;
  height: 300px;
  position: relative;
  padding: 0;
}

.neck,
.hair,
.ears,
.mouth,
.leg,
.nose,
.eyes,
.backgrounds,
.accessories {
  position: absolute;
  width: 100%;
  height: 100%;
}

.neck {
  z-index: 2;
}
.ears {
  z-index: 1;
}
.hair {
  z-index: 5;
}
.eyes {
  z-index: 6;
}
.nose {
  z-index: 4;
}

.mouth {
  z-index: 7;
}

.accessories {
  z-index: 8;
}

.headphone{
  z-index: 3;
}

.btn {
  height: 1.8rem;
  border: none;
  margin: 5px;
  padding: 0 10px;
  border-radius: 15px;
  background-color: #ffffff55;
  transition: 0.1s all linear;
  font-size: 1.2rem;
}
.buttons{
  margin-top: 10px;
}

.selected {
  background-color: #aa33dd !important;
  color: #ccc;
}

@media (max-width: 750px) {
  .alpaca-cont {
    grid: auto auto / 1fr;
    padding: 0;
  }
  .settings-cont {
    text-align: center;
  }
  h2 {
    font-size: 1.2rem;
  }
  .btn {
    font-size: 0.9rem;
  }
}
